.report {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, .7);
	display: -webkit-flex;
	display: flex;
}

.report-inner {
	margin: 20px auto;
	background: white;
	width: 800px;
	max-width: 100vw;
	overflow: auto;
	border-radius: 4px;
	padding: 16px;
}

.close-report,
.print-report {
	display: block;
	width: 100%;
	text-align: right;
	color: var(--fucsia);
	cursor: pointer;
}

.print-report {
	text-align: left;
}

.report h4,
.report h3,
.report p {
	margin-bottom: 1em;
}

.report h3 {
	text-align: center;
}

.debt-summary,
.payment-schedule {
	width: 100%;
	overflow: auto;
	margin-bottom: 2em;
}

.report-inner table {
	width: 100%;
	border-collapse: collapse;
}

.report-inner thead,
.report-inner tfoot {
	color: white;
	background: var(--purple);
}

.report-inner td,
.report-inner th {
	padding: 8px 16px;
	border-right: 1px solid #ccc;
	text-align: right;
}

.debt-summary thead tr:first-child th {
	font-size: 1.3em;
	padding: 16px 4px;
	text-align: center;
}

.report-inner tbody tr:nth-child(even) {
	background: #eee;
}

.report-inner tbody tr:hover {
	background: var(--lightpink);
}

.report-inner tbody td:hover {
	background: var(--pink);
}
@media print {

	.close-report,
	.print-report {
		display: none !important;
	}

	table {
		width: 100%;
	}

	.report-inner {
		padding: 8px;
	}

	h4,
	h3,
	p {
		margin-bottom: 1em !important;
	}

	.BalanceChart {
		margin-bottom: 8em;
	}

	.accordion {
		display: none !important;
	}

	.accordion__content {
		max-height: none !important;
	}
}
:root {
