}

.BalanceChartTwin .legend {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.BalanceChartTwin .legend>* {
  position: relative;
  padding-left: 20px;
}

.BalanceChartTwin .legend .add {
  margin-left: 30px;
}

.BalanceChartTwin .legend .min:before,
.BalanceChartTwin .legend .add:before {
  content: " ";
  position: absolute;
  height: 12px;
  width: 12px;
  left: 0;
  border-radius: 4px;
  top: 3px;
  background: #434343;
}

.BalanceChartTwin .legend .add:before {
  background: #ff6f31;
}

